import {useState, useEffect} from 'react';
import styled from 'styled-components';

const default_data = [
    "data:image/png;base64,",
    "https://critteraid.org/wp-content/uploads/2021/01/Interested-in-Adoption_.jpg",
    "data:image/jpeg;base64,",
]
const Cont = styled.div`
display:inline-flex;
flex-direction:column;
`;
const Menu = styled.img`
width:100px;
height:100px;
object-fit:cover;
`;

const Controls = styled.div`
display:flex;
justify-content:space-between;
`;

const CtrlButtons = styled.button`
background-color:#FFD7F1;
border-radius:  5px;
font-size:20px;
color: white;
border: none;`;

const Carousel = ({
    imgdata=default_data
}) => {
    const [ind, setInd] = useState(0)

    const HandleImageChange = (i) =>{
       if(i<0){
           i = 0;
       }
       if(i>imgdata.length-1){
           i = imgdata.length-1;
       }
        setInd(i);

    }
    return <Cont>
        Some resources
        <Menu src={imgdata[ind]}/>
        <Controls>
            <CtrlButtons onClick={()=>HandleImageChange(ind-1)}>
next
            </CtrlButtons>
            <CtrlButtons onClick={()=>HandleImageChange(ind+1)}>
               back 
            </CtrlButtons>
        </Controls>
    </Cont>

}

export default Carousel;